
.wrapper {
  background: #eee;
  position: relative;
}
.inner-wrapper {
  height: 0;
}
.spin {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  margin: auto;
}
img {
  width: 100%;
}
img.img-pre-size {
  position: absolute;
  top: 0;
  left: 0;
}


